"use client";

import React from "react";
import { Star } from "lucide-react";

interface Anime {
  mal_id: number;
  title: string;
  images: {
    jpg: {
      large_image_url?: string;
      image_url: string;
    };
  };
  score?: number;
  episodes?: number | null | undefined;
  type?: string | null;
}

interface AnimeCardProps {
  anime: Anime;
}

export function AnimeCard({ anime }: AnimeCardProps) {
  const imageUrl =
    anime.images?.jpg?.large_image_url ||
    anime.images?.jpg?.image_url ||
    "https://placehold.co/300x450/333333/FFFFFF?text=No+Image";

  const displayScore = anime.score != null ? anime.score.toFixed(1) : "N/A";
  const displayEpisodes =
    anime.episodes != null ? `${anime.episodes} Ep` : "Ongoing";
  const displayType = anime.type || "Anime";

  // Label yang muncul di kiri atas
  const typeLabel =
    displayType.toLowerCase() === "movie"
      ? "Movie"
      : displayType.toLowerCase() === "tv"
      ? "TV Anime"
      : displayType;

  return (
    <div className="group relative flex flex-col h-full cursor-pointer rounded-xl overflow-hidden bg-card shadow-sm hover:shadow-xl transition-all duration-300 hover:-translate-y-1">
      {/* ======================
          IMAGE WRAPPER
      ======================= */}
      <div className="relative w-full aspect-[2/3] overflow-hidden rounded-t-xl">
        <img
          src={imageUrl}
          alt={anime.title}
          className="h-full w-full object-cover transition-transform duration-500 group-hover:scale-110"
          loading="lazy"
          onError={(e: any) =>
            (e.target.src =
              "https://placehold.co/300x450/333333/FFFFFF?text=No+Image")
          }
        />

        {/* Overlay */}
        <div className="absolute inset-0 bg-black/20 opacity-0 group-hover:opacity-100 transition-opacity duration-300" />

        {/* üî• TYPE BADGE (LEFT TOP) */}
        <div className="absolute top-2 left-2 bg-black/70 backdrop-blur-sm border border-white/10 px-2 py-1 rounded-md text-xs font-bold text-white shadow-sm z-10">
          {typeLabel}
        </div>

        {/* ‚≠ê SCORE BADGE (RIGHT TOP) */}
        {anime.score != null && (
          <div className="absolute top-2 right-2 bg-black/70 backdrop-blur-sm border border-white/10 px-2 py-1 rounded-md text-xs font-bold text-white shadow-sm z-10 flex items-center gap-1">
            <Star size={12} className="text-yellow-400 fill-yellow-400" />
            {displayScore}
          </div>
        )}
      </div>

      {/* ======================
          CONTENT
      ======================= */}
      <div className="p-3 flex flex-col flex-1 bg-card border-t border-border/50">
        <h3 className="font-semibold text-sm sm:text-base line-clamp-2 text-foreground group-hover:text-primary transition-colors mb-1">
          {anime.title}
        </h3>

        <div className="mt-auto flex items-center justify-between text-xs text-muted-foreground pt-1">
          {/* Episodes */}
          <span>{displayEpisodes}</span>

          {/* Type (secondary badge bawah) */}
          <span className="font-medium bg-muted px-2 py-0.5 rounded-full text-foreground/80">
            {typeLabel}
          </span>
        </div>
      </div>
    </div>
  );
}
