'use client'

import { useState, useEffect } from 'react'
import { getPopularAnime, Anime } from '@/lib/api'
import { AnimeCard } from '@/components/anime-card'
import Link from 'next/link'
import { Eye } from 'lucide-react'

// Placeholder Component untuk Navbar (Sederhana)
const Navbar = () => (
  <div className="sticky top-0 z-40 w-full border-b bg-background/95 backdrop-blur supports-[backdrop-filter]:bg-background/60 h-14">
    <div className="max-w-7xl mx-auto flex h-full items-center px-4">
      <span className="font-bold text-xl">FeinimeList</span>
    </div>
  </div>
);

// Placeholder Component untuk SkeletonGrid (Sederhana)
const SkeletonGrid = () => (
  <div className="grid grid-cols-2 md:grid-cols-3 lg:grid-cols-4 xl:grid-cols-5 gap-4 md:gap-6">
    {[...Array(10)].map((_, i) => (
      <div key={i} className="space-y-3">
        <div className="aspect-[2/3] w-full rounded-xl bg-muted/50 animate-pulse" />
        <div className="h-4 w-3/4 rounded bg-muted/50 animate-pulse" />
        <div className="h-3 w-1/2 rounded bg-muted/50 animate-pulse" />
      </div>
    ))}
  </div>
);

export default function PopularPage() {
  const [animes, setAnimes] = useState<Anime[]>([])
  const [loading, setLoading] = useState(true)

  useEffect(() => {
    const fetchPopular = async () => {
      setLoading(true)
      try {
        const data = await getPopularAnime()
        setAnimes(data.data || [])
      } catch (error) {
        console.error("Gagal mengambil anime populer:", error)
      } finally {
        setLoading(false)
      }
    }
    fetchPopular()
  }, [])

  return (
    <main className="min-h-screen bg-background">
      <Navbar />

      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-12">
        <div className="text-center md:text-left mb-10">
          <h1 className="text-4xl font-bold mb-2 text-foreground">Popular Anime</h1>
          <p className="text-muted-foreground">
            Jelajahi seri anime paling populer saat ini
          </p>
        </div>

        {loading ? (
          <SkeletonGrid />
        ) : animes.length > 0 ? (
          <div className="grid grid-cols-2 md:grid-cols-3 lg:grid-cols-4 xl:grid-cols-5 gap-4 md:gap-6 animate-in fade-in slide-in-from-bottom-4 duration-500">
            {animes.map((anime, index) => (
              <Link 
                key={`${anime.mal_id}-${index}`} 
                href={`/anime/${anime.mal_id}`} 
                className="group relative block"
              >

                {/* Kartu Anime */}
                <AnimeCard anime={anime} />

                {/* TOMBOL VIEW DETAILS (Overlay) */}
                <div className="absolute inset-0 flex items-center justify-center opacity-0 group-hover:opacity-100 transition-all duration-300 bg-black/60 rounded-xl pointer-events-none z-10">
                  <span className="inline-flex h-10 items-center justify-center rounded-md border border-white/30 bg-white/20 backdrop-blur-md px-6 text-sm font-bold text-white shadow-lg transform translate-y-4 group-hover:translate-y-0 transition-transform duration-300">
                    <Eye size={18} className="mr-2" /> Lihat Detail
                  </span>
                </div>

              </Link>
            ))}
          </div>
        ) : (
          <div className="text-center py-20 bg-secondary/20 rounded-xl border border-dashed border-border">
            <p className="text-muted-foreground">Tidak ditemukan anime populer</p>
          </div>
        )}
      </div>

      {/* Footer */}
      <footer className="border-t border-border bg-card mt-auto">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8 sm:py-12">
          <div className="flex flex-col md:flex-row justify-between items-center gap-6">

            {/* Brand */}
            <div className="text-center md:text-left">
              <span className="font-bold text-xl tracking-tight">FeinimeList</span>
            </div>

            {/* Navigation Links */}
            <nav className="flex flex-wrap justify-center gap-x-6 gap-y-3 text-sm text-muted-foreground">
              {['Home', 'Popular', 'Trending', 'About', 'Contact', 'FAQ'].map((item) => (
                <a key={item} href="#" className="hover:text-primary transition-colors">
                  {item}
                </a>
              ))}
            </nav>

            {/* Social Icons */}
            <div className="flex items-center gap-4">
              <a href="#" className="text-muted-foreground hover:text-[#1DA1F2] transition-colors">
                <span className="sr-only">Twitter</span>
                <svg className="w-5 h-5" viewBox="0 0 24 24" fill="currentColor">
                  <path d="M23.954 4.569c-.885.389-1.83.654-2.825.775 1.014-.611 1.794-1.574 2.163-2.723-.951.564-2.005.974-3.127 1.195-.897-.955-2.178-1.55-3.594-1.55-2.717 0-4.92 2.204-4.92 4.917 0 .39.045.765.127 1.124-4.087-.205-7.72-2.164-10.148-5.144-.424.722-.666 1.561-.666 2.475 0 1.708.87 3.214 2.188 4.099-.807-.025-1.566-.248-2.229-.616v.061c0 2.385 1.693 4.374 3.946 4.827-.413.111-.849.171-1.296.171-.314 0-.615-.03-.916-.086.631 1.953 2.445 3.376 4.604 3.416-1.68 1.319-3.809 2.105-6.102 2.105-.396 0-.779-.023-1.158-.067 2.189 1.402 4.768 2.217 7.548 2.217 9.051 0 14-7.496 14-13.986 0-.21 0-.42-.015-.63 1.009-.73 1.884-1.64 2.584-2.675z" />
                </svg>
              </a>

              <a href="#" className="text-muted-foreground hover:text-[#5865F2] transition-colors">
                <span className="sr-only">Discord</span>
                <svg className="w-5 h-5" viewBox="0 0 71 55" fill="currentColor">
                  <path d="M60.1 4.55A59 59 0 0 0 46.92 0c-.65 1.14-1.39 2.59-1.9 3.74a42 42 0 0 0-17 0C27.5 2.6 26.76 1.15 26.1 0A58.8 58.8 0 0 0 10.9 4.55C2.68 19.28.08 33.43 1.3 47.36c11.04 8.16 21.56 6.06 21.56 6.06 1.44-1.84 2.56-3.78 3.44-5.7-6.16-1.84-8.52-4.52-8.52-4.52.72.48 1.44.92 2.16 1.3 4.92 2.52 10.12 3.78 15.44 3.78s10.52-1.26 15.44-3.78c.72-.38 1.44-.82 2.16-1.3 0 0-2.36 2.68-8.52 4.52.88 1.92 2 3.86 3.44 5.7 0 0 10.52 2.1 21.56-6.06 1.22-13.92-1.38-28.07-9.6-42.8Z" />
                </svg>
              </a>
            </div>
          </div>

          {/* COPYRIGHT */}
          <div className="border-t border-border mt-8 pt-8 text-center text-muted-foreground text-sm">
            <p>FeinimeList ¬© 2025. All rights reserved.</p>
          </div>
        </div>
      </footer>
    </main>
  )
}
